#updraftcentral_dashboard {
	#updraft-central-content-container .updraft-central-sidebar #updraft-menu-item-eum .dashicons.dashicons-info:before {
			background: url(../../images/icons/eum-svg.svg) no-repeat left center;
			background-size: contain;
	}

	#updraftcentral_dashboard_existingsites_container {
		clear: left;
	}

	#log-export {
		display: none;
	}

	/**
	* Basic styles applied to the Easy Updates Manager container
	*/
	.updraftcentral_eum_rows {
		font-size: 15px;
		margin-top: 20px;

		p {
			margin-bottom: 1rem;
			padding: 0;
		}

		h3 {
			margin-top: 1rem;
		}

		#eum-adv-settings-menu {
			display: none;
		}

		#eum-adv-settings {
			.eum-advanced-settings-container.anonymize-updates {
				label {
					display: block;
				}
			}
			.eum-advanced-settings-container {
				.eum-hidden {
					display: none;
				}
			}
		}

		.form-table th {
			padding: 12px;
		}

		.eum-not-found-message {
			margin-top: 20px;
			margin-bottom: 50px;
		}

		.eum-not-installed,
		.eum-not-activated {
			margin-bottom: 20px;
		}

		button#btn-install-activate,
		button#btn-activate {
			margin-bottom: 20px;
		}

		.display-none,
		.eum_spinner {
		  	display: none;
		}
		.screen-reader-text, .screen-reader-text span, .ui-helper-hidden-accessible {
			border: 0;
			clip: rect(1px, 1px, 1px, 1px);
			-webkit-clip-path: inset(50%);
			clip-path: inset(50%);
			height: 1px;
			margin: -1px;
			overflow: hidden;
			padding: 0;
			position: absolute;
			width: 1px;
			word-wrap: normal !important;
		}


		.tablenav-pages {
			margin: 0 auto;
			display: block;
			cursor: default;
			float: right;
		}

		.tablenav-pages span,
		.tablenav-pages a {
			margin: 0 2px;
		}

		.tablenav-pages #current-page-selector {
			margin: 0 !important;
			padding: 4px 0 !important;
			height: auto !important;
			line-height: normal;
			min-width: 30px;

		}

		.tablenav-pages a,
		.tablenav-pages-navspan {
			background-color: #EFEFEF;
			display: inline-block;
			padding: 3px 6px;
			font-weight: bold;
			font-size: 1rem;
		}

		.subsubsub li {
			display: inline-block;
		}

		.subsubsub li > a {
			padding: 0 5px;
		}

		#post-query-submit, #doaction, #doaction2 {
			font-size: 0.938rem;
			font-family: "Source Sans Pro", Roboto, "Helvetica Neue", Arial, sans-serif;
			font-weight: 300;
			border-radius: 3px;
			padding: .4rem 1rem;
			line-height: 1.5;
			display: inline-block;
			color: #ffffff;
			background-color: #DE6726;
			border: none;
		}
		#search-filter {
			.alignright {
				float: none;
				clear: left;
				width: 100%;
				margin-left: 0;
			}
		}
		#logs-filter {
			width: 100%;

			.alignleft {
				margin-bottom: 10px;
				clear: left;
				min-width: 100%;
				max-width: 100%;

				label {
					display: none;
				}

				select {
					width: 16% !important;
					padding-right: 5px;
				}
			}
			&:after {
				content: "";
				display: table;
				clear: both;
			}
		}

		.bulkactions {
			width: 48%;
			text-align: left;

			label {
				display: block;
			}

			#bulk-action-selector-top,
			#bulk-action-selector-bottom {
				width: 50%;
			}
		}

		table td,
		table th {
			vertical-align: top;
			padding: 5px;
		}

		#the-list {

			input[type="checkbox"] {
				position: absolute;
				top: 10px;
			}

			.eum-themes-name-actions,
			.eum-plugins-name-actions {
				display: block;
				margin: 0 auto;
				text-align: left;

				h3 {
					margin: 10px 0;
					padding: 0;
					font-size: 1.2rem !important;
					font-weight: bold;
				}

				h4 {
					font-size: 1rem !important;
					margin: 5px 0;
					padding: 0;
				}
			}
		}

		.wp-list-table.plugins .plugin-title,
		.wp-list-table.plugins .theme-title {
			padding-right: 12px;
			white-space: normal!important;
			min-width: 200px;
		}

		.plugin-title {
			img {
				width: 85px;
			}
			span.dashicons {
				width: 85px;
				height: 85px;
				font-size: 3rem;
				line-height: 2;
			}
		}

		#save-cron-schedule {
			display: block;
			margin: 10px 0;
		}

		input[type=time] {
			margin-left: 5px;
		}

		@media (max-width: 1024px) {
			#logs-filter {
				width: 100%;
			}

			.tablenav-pages {
				float: left;
				margin-bottom: 10px;
			}

			.bulkactions {
				width: auto;
				&.alignleft {
					float: none;
				}
			}

			.updraftcentral_row_site_buttons .btn {
				display: block;
				cursor: pointer;
			}
		}

		.toggle-wrapper button,
		.toggle-wrapper a {
			padding: 5px 10px;
			display: inline-block;
			background: #f8f8f8;
			border-right: 0;
			border-color: #CCC;
			font-size: 12px;
			border-style: solid;
			border-width: 1px;
			cursor:pointer;
			align-items: flex-start;
			text-align: center;
			text-rendering: auto;
			color: initial;
			letter-spacing: normal;
			word-spacing: normal;
			text-transform: none;
			text-indent: 0px;
			text-shadow: none;
			display: inline-block;
			text-align: start;
			margin: 0em;
			margin-right:-4px;
			-webkit-appearance: button;
			appearance: button;
			&:first-child,
			&:first-of-type {
				border-radius: 5px 0px 0px 5px;
			}
			&:last-child {
				border-radius: 0px 5px 5px 0px;
				border-right: 1px solid #CCC;
			}
			&:not(:last-child) {
				border-right: none;
			}

			&.eum-toggle-button {
				background-color: rgb(248, 248, 248);
				color: #000;
			}
			&.eum-toggle-button:hover {
				background-color: #DDDDDD;
				color: #000;
				box-shadow: 0 1px 0 #AAAAAA;
			}

			&.eum-active {
				background: #25af76;
				border-color: #1a8a59;
				box-shadow: 0 1px 0 #1a8a59;
				color: #FFF;
				-webkit-appearance: inherit;
				appearance: inherit;
			}
			&.eum-active:hover {
				color: #FFF;
				background: #25af3c;
				border-color: #1e892f;
				box-shadow: 0 1px 0 #249637;
			}

			@media only screen and (max-width: 600px) {
				display: block;
				width: 100%;
				border-radius: 0 0 0 0 !important;
				border: 1px solid #CCC !important;
			}
		}

		@media (max-width: 450px) {
			.tablenav-pages {
				float: none;

				span.tablenav-pages-navspan,
				a {
					display: block;
					margin: 0 0 5px 0;
					padding: 0;
				}

				span.paging-input {
					margin-bottom: 5px;
					display: block;
				}
			}
		}

		@media screen and (max-width: 782px) {
			.wp-list-table thead th.column-primary {
				width: 100%;
			}

			.wp-list-table th.column-primary~th, .wp-list-table tr:not(.inline-edit-row):not(.no-items) td.column-primary~td:not(.check-column) {
				display: none;
			}

			.wp-list-table tr:not(.inline-edit-row):not(.no-items) td:not(.check-column) {
				position: relative;
				clear: both;
				display: block;
				width: auto!important;
			}
		}
	}
	.updraftcentral_site_row {

		.eum-dashboard-app {
			position: relative;
		}

		#eum-save-settings-warning {
			position: absolute;
			right: 20px;
			top: 100px;
			width: auto;
			display: none;
		}

		.eum-dashboard-app .warning,
		#eum-save-settings-warning.warning{
			color: #333;
			padding: 5px !important;
			border: 1px solid #F00;
			background-color: #f0a5a4;
			border-radius: 5px;
			margin: 15px 0;

			p {
				margin: 0;
			}
		}

		.btn-primary {
			.dashicons-admin-settings:before {
				width: 24px;
				background: url(../../images/icons/admin-settings-icon.svg) no-repeat center center;
			}

			.dashicons-admin-logs:before {
				width: 24px;
				background: url(../../images/icons/admin-logs-icon.svg) no-repeat center center;
			}
			.dashicons-admin-advanced:before {
				width: 24px;
				background: url(../../images/icons/admin-advanced-icon.svg) no-repeat center center;
			}
		}
	}

	@media (max-width: 1279px) {
		.updraftcentral_site_row {
			#logs-filter {
				.alignleft {
					select {
						width: 48%;
						padding-right: 20px;
					}
				}
			}
		}
	}

}

@media (min-width: 549px) {
	.updraftcentral_dashboard {
		div.updraftcentral_eum_rows {
			.show_on_mobile_sizes {
				display: none !important;
			}
		}	
	}
}

@media (max-width: 548px) {
	.updraftcentral_dashboard {
		div.updraftcentral_eum_rows {
			.show_on_default_sizes {
				display: none !important;
			}
		}	
	}
}

#eum-save-general-settings {
	margin-top: 10px;
}

